// eslint-disable-next-line
import React, { Component } from 'react';

let Text;

if (parseInt(React.version, 10) < 16) {
	Text = props => {
		return <span>{ props.children }</span>;
	};
} else {
	Text = props => {
		return props.children;
	};
}




export default Text;



