import React, { Component } from 'react';
import PropTypes from 'prop-types';

import slot from '../../Common/Util/slot';

import classNames from 'classnames';

import './scss/slide.css';
import './scss/slide-effects.css';


class Slide extends Component {
	static CLASSES = 'rmd-trans-slide';
	static CLASSES_ANIMATION_RTL = 'rmd-trans-slide__right-to-left';
	static CLASSES_ANIMATION_LTR = 'rmd-trans-slide__left-to-right';
	static CLASSES_ANIMATION_TTB = 'rmd-trans-slide__top-to-bottom';
	static CLASSES_ANIMATION_BTT = 'rmd-trans-slide__bottom-to-top';
	static CLASSES_ANIMATION_RUN = 'rmd-trans-slide__run';
	static CLASSES_ANIMATION_RESET = 'rmd-trans-slide__reset';

	static CLASSES_BLOCK_ACTIVE = 'rmd-trans-slide--active';
	static CLASSES_BLOCK_PREV = 'rmd-trans-slide--prev';

	constructor(props){
		super(props);

		this.state = {
			activeKey: this.props.activeKey,
			inTransition: false,
			inTransitionReset: false,
		};

		this.stateCounter = 0;

		this._refs = {
			$container: null,
		};

		this.handleRef = this.handleRef.bind(this);
	}

	componentWillReceiveProps(nextProps) {

		const activeKey = nextProps.activeKey;
		const prevKey = this.state.activeKey;

		if (activeKey !== prevKey) {
			if (this._refs.$container) {
				const $container = this._refs.$container;

				this.stateCounter += 1;
				const cState = this.stateCounter;

				const onTransitionEnd = e => {
					$container.removeEventListener('transitionend', onTransitionEnd);

					if (cState !== this.stateCounter) {
						return;
					}

					this.setState({
						inTransition: false,
						activeKey,
					});
				};

				$container.addEventListener('transitionend', onTransitionEnd);
			}

			if (this.state.inTransition) {
				this.setState({
					activeKey,
					inTransition: false,
					inTransitionReset: true,
				});

				requestAnimationFrame(() => {
					requestAnimationFrame(() => {
						this.setState({
							inTransition: true,
							inTransitionReset: false,
						});
					});
				});
			} else {
				this.setState({
					activeKey,
					inTransition: true,
					inTransitionReset: false,
				});
			}

		}
	}

	handleRef(el) {
		this._refs.$container = el;
	}

	render() {

		let { active, prev } = slot(this.props.children);

		if (this.state.inTransitionReset) {
			active = prev;
			prev = null;
		}

		return (
			<div
				className={ classNames( this.constructor.CLASSES, this.props.class, this.props.className, {
					[ this.constructor.CLASSES_ANIMATION_RTL ]: this.props.animation === 'right-to-left',
					[ this.constructor.CLASSES_ANIMATION_LTR ]: this.props.animation === 'left-to-right',
					[ this.constructor.CLASSES_ANIMATION_TTB ]: this.props.animation === 'top-to-bottom',
					[ this.constructor.CLASSES_ANIMATION_BTT ]: this.props.animation === 'bottom-to-top',
					[ this.constructor.CLASSES_ANIMATION_RUN ]: this.state.inTransition,
					[ this.constructor.CLASSES_ANIMATION_RESET ]: ! this.state.inTransition,
				}) }

				ref={ this.handleRef }
			>
				<div
					className={ this.constructor.CLASSES_BLOCK_ACTIVE }
				>
					{ active }
				</div>
				{
					this.state.inTransition && prev ?
						<div
							className={ this.constructor.CLASSES_BLOCK_PREV }
							ref={ this.handleNextMonthRef }
						>
							{ prev }
						</div>
						: null
				}
			</div>
		)
	}
}

Slide.propTypes = {
	animation: PropTypes.oneOf(['right-to-left', 'left-to-right', 'top-to-bottom', 'bottom-to-top']).isRequired,
	activeKey: PropTypes.any.isRequired,
};

export default Slide;



