// eslint-disable-next-line
import React, { Component } from 'react';

import './scss/input-error.css';

const theme = {
	CLASSES_ERROR: 'rmd-form-input--error',
	CLASSES_WARNING: 'rmd-form-input--warning',
};

const Error = ({ _theme = theme, children }) => {
	return (
		<div className={ _theme.CLASSES_ERROR }>
			{ children }
		</div>
	);
};

export const Warning = ({ _theme = theme, children }) => {
	return (
		<div className={ _theme.CLASSES_WARNING }>
			{ children }
		</div>
	);
};

export default Error;



