import React, { Component } from 'react';

import PropTypes from 'prop-types';

import slot from '../../Common/Util/slot';

import {
	handleMouseFocusMouseDown,
	handleMouseFocusFocus,
	handleMouseFocusBlur,
} from '../../Common/Util/setMouseFocus';


import classNames from 'classnames';

import './scss/select.css';
import './scss/select-effects.css';

require('../../Common/Polyfill/element-closest');

export const theme = {
	CLASS_MENU_ITEM: 'rmd-menu-popup--item',
	CLASS_WRAPPER: 'rmd-select--wrapper',
	CLASS_MENU_OPEN: 'rmd-menu-popup--wrapper__open',
	CLASS_WRAPPER_ACTIVE: 'rmd-select--wrapper__active',

	CLASSES: 'rmd-select',
	CLASSES_WRAPPER: 'rmd-select--wrapper',
	CLASSES_WRAPPER_DISABLED: 'rmd-select--wrapper__disabled',
	CLASSES_WRAPPER_DENSE: 'rmd-select--wrapper__dense',
	CLASSES_WRAPPER_DARK: 'rmd-select--wrapper__dark',
	CLASSES_WRAPPER_WITH_ICON: 'rmd-select--wrapper__with-icon',
	CLASSES_POPUP_CONTAINER: 'rmd-menu-popup--container',
	CLASSES_CONTENT: 'rmd-select--content',
	CLASSES_LABEL: 'rmd-select--label',
	CLASSES_ICON_INNER_RIGHT: 'rmd-select--icon-inner-right',
	CLASSES_FOOTER: 'rmd-select--footer',

	CLASSES_ICON_SIGNIFIER_WRAPPER: 'rmd-select--icon-wrapper',
	CLASSES_ICON_SIGNIFIER: 'rmd-select--icon',

	CLASSES_ICON_DOWN: 'rmd-select--icon-down',


	CLASSES_WRAPPER_HAS_ERROR: 'rmd-select--wrapper__has-error',
	CLASSES_WRAPPER_HAS_WARNING: 'rmd-select--wrapper__has-warning',

	MENU_HIDE_ANIMATION_TIME: 450 + 200,
};


class Select extends Component {
	constructor(props) {
		super(props);

		this.state = {
			menuShow: props.openImmediately,
		};

		this.realMenuShow = this.state.menuShow;

		this.theme = theme;

		this._refs = {
			$button: undefined,
			$wrapper: undefined,
		};

		this.id = 'select-' + Math.random();

		this.isActive = false;


		this.handleButtonRef = this.handleButtonRef.bind(this);
		this.handleWrapperRef = this.handleWrapperRef.bind(this);


		this.handleMouseDown = this.handleMouseDown.bind(this);
		this.handleFocus = this.handleFocus.bind(this);
		this.handleBlur = this.handleBlur.bind(this);

		this.handleClick = this.handleClick.bind(this);
		this.handleWrapperClick = this.handleWrapperClick.bind(this);
		this.handleDocumentMouseDown = this.handleDocumentMouseDown.bind(this);
	}

	componentDidMount() {
		if (this.props.openImmediately) {
			this.setListeners();
		}
	}

	componentWillUnmount() {
		this.setListeners(false);
	}

	componentWillUpdate(nextProps, nextState) {
		if (this.state.menuShow !== nextState.menuShow) {
			if (nextState.menuShow) {
				this.setListeners();
			} else {
				this.setListeners(false);
			}
		}
	}

	setListeners(add = true) {
		if (add) {
			document.addEventListener('mousedown', this.handleDocumentMouseDown);
		} else {
			document.removeEventListener('mousedown', this.handleDocumentMouseDown);
		}
	}


	handleMouseDown(e) {
		const { _refs: refs, props } = this;

		handleMouseFocusMouseDown(e, refs.$button, this);

		if (props.onMouseDown) {
			return props.onMouseDown(e);
		}
	};

	handleFocus(e) {
		const { _refs: refs, props } = this;

		handleMouseFocusFocus(e, refs.$button, this);

		if (refs.$wrapper) {
			const $wrapper = refs.$wrapper;

			$wrapper.classList.add(theme.CLASS_WRAPPER_ACTIVE);
			this.isActive = true;
		}

		if (props.onFocus) {
			return props.onFocus(e);
		}
	};

	handleBlur(e) {
		const { _refs: refs, props } = this;

		const valid = handleMouseFocusBlur(e, refs.$button, this);

		if (valid === false) {
			return valid;
		}

		if (refs.$wrapper) {
			const $wrapper = refs.$wrapper;

			$wrapper.classList.remove(theme.CLASS_WRAPPER_ACTIVE);
			this.isActive = false;
		}


		if (props.onBlur) {
			return props.onBlur(e);
		}
	};


	showMenu() {
		this.realMenuShow = true;

		if (this.state.menuShow === true) {
			this.setState({ menuShow: false }, () => {
				this.setState({ menuShow: true });
			});
		} else {
			this.setState({ menuShow: true });
		}


		const { _refs: refs, theme } = this;

		if (refs.$wrapper) {
			const $wrapper = refs.$wrapper;

			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					$wrapper.classList.add(theme.CLASS_MENU_OPEN);
				});
			});
		}
	}


	hideMenu() {
		this.realMenuShow = false;

		setTimeout(() => {
			if ( this.realMenuShow || ! this.state.menuShow) {
				return;
			}

			this.setState({ menuShow: false });
		}, this.theme.MENU_HIDE_ANIMATION_TIME);


		const { _refs: refs, theme } = this;

		if (refs.$wrapper) {
			const $wrapper = refs.$wrapper;

			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					$wrapper.classList.remove(theme.CLASS_MENU_OPEN);
				});
			});
		}

		if (refs.$button) {
			const $button = refs.$button;

			$button.focus();
		}
	}


	handleClick(e) {
		if (this.realMenuShow) {
			this.hideMenu();
		} else {
			this.showMenu();
		}

	}

	handleDocumentMouseDown(e) {
		if (e.target.closest('.' + this.theme.CLASS_WRAPPER) !== this._refs.$wrapper) {
			this.hideMenu();
		}
	}

	handleWrapperClick(e) {
		if (! this.state.menuShow) {
			return;
		}

		if (e.target.closest('.' + this.theme.CLASS_WRAPPER) === this._refs.$wrapper &&
			e.target.closest('.' + this.theme.CLASS_MENU_ITEM)) {
			this.hideMenu();
		}
	}

	handleButtonRef(el) {
		this._refs.$button = el;
	}

	handleWrapperRef(el) {
		this._refs.$wrapper = el;
	}

	render() {

		const { style, dark, disabled, openImmediately, iconSpace, size,
			btnStyle, wrapperStyle, btnClass, wrapperClass, children,
			...props
		} = this.props;

		const _theme = this.theme;

		const { main, menu, label, icon, footer } = slot(children);

		const popupContainer = (
			<div className={ _theme.CLASSES_POPUP_CONTAINER }>
				<button
					{ ...props }
					disabled={ disabled }
					id={ this.id }
					style={ style || btnStyle }
					className={ classNames(_theme.CLASSES, props.class, props.className, btnClass) }
					ref={ this.handleButtonRef }
					onMouseDown={ this.handleMouseDown }
					onFocus={ this.handleFocus }
					onBlur={ this.handleBlur }
					onClick={ this.handleClick }
				>
					<div className={ _theme.CLASSES_CONTENT }>
						{ main }
					</div>
					<div className={ _theme.CLASSES_ICON_INNER_RIGHT }>
						<svg viewBox="0 0 24 24" className={ _theme.CLASSES_ICON_DOWN }><path d="M7 10l5 5 5-5z"></path></svg>
					</div>
				</button>
				{ this.state.menuShow ? menu : null }
			</div>
		);

		return (
			<div
				style={ wrapperStyle }
				className={ classNames(_theme.CLASSES_WRAPPER, wrapperClass, {
					[ _theme.CLASS_WRAPPER_ACTIVE ]: this.isActive,
					[ _theme.CLASSES_WRAPPER_DISABLED ]: disabled,
					[ _theme.CLASSES_WRAPPER_DENSE ]: size === 'dense',
					[ _theme.CLASSES_WRAPPER_DARK ]: dark,
					[ _theme.CLASSES_WRAPPER_WITH_ICON ]: icon || iconSpace,
				}) }
				ref={ this.handleWrapperRef }
				onClick={ this.handleWrapperClick }
			>
				{ label ?
					<label
						className={ _theme.CLASSES_LABEL }
						htmlFor={ this.id }
					>
						{ label }
					</label>
					: null
				}

				{ icon ?
					<div className={ _theme.CLASSES_ICON_SIGNIFIER_WRAPPER }>
						<label
							htmlFor={ this.id }
							className={ _theme.CLASSES_ICON_SIGNIFIER }
						>
							{ icon }
						</label>
						{ popupContainer }
					</div>
					: popupContainer
				}

				{ footer ?
					<div className={ _theme.CLASSES_FOOTER }>
						{ footer }
					</div>
					: null
				}
			</div>
		);
	}
}

Select.propTypes = {
	openImmediately: PropTypes.bool,
	btnStyle: PropTypes.any,
 	wrapperStyle: PropTypes.any,
 	btnClass: PropTypes.string,
 	wrapperClass: PropTypes.string,
	iconSpace: PropTypes.bool,
	dark: PropTypes.bool,
	size: PropTypes.oneOf(['normal', 'dense']),
	disabled: PropTypes.bool,
};


export default Select;



