// eslint-disable-next-line
import React, { Component } from 'react';

let Block;

if (parseInt(React.version, 10) < 16) {
 	Block = props => {
		return <div>{ props.children }</div>;
	};
} else {
 	Block = props => {
		return props.children;
	};
}




export default Block;



