import React from 'react';
import Day from './Day';

import PropTypes from 'prop-types';


import isThisMonth from 'date-fns/is_this_month';
import startOfMonth from 'date-fns/start_of_month';
import getDaysInMonth from 'date-fns/get_days_in_month';
import isPast from 'date-fns/is_past';
import isSameMonth from 'date-fns/is_same_month'
import format from 'date-fns/format';


export const listMontDates = (month, today, activeDay) => {
	const days = getDaysInMonth(month);
	const strStart = format(month, 'YYYY-MM-');
	const startDay = startOfMonth(month);

	const startWeekDay = startDay.getDay();
	const todayDay = today.getDate();
	const activeDayDay = activeDay.getDate();

	let isPastDay;
	let isToday;
	let isActive;

	if (! isThisMonth(month)) {
		isToday = false;
		isPastDay = isPast(month);
	}

	if (! isSameMonth(activeDay, month)) {
		isActive = false;
	}

	const list = [];

	const push = (day, dayStr, weekday, isToday, isPast, isActive) => {
		list.push({
			shortName: day,
			longName: strStart + dayStr,
			weekday,
			isToday,
			isPast,
			isActive,
		});
	};

	for (let i = 1; i < 10; i++) {
		push(i, '0' + i, (startWeekDay + i - 1) % 7, isToday === undefined ? todayDay === i : isToday,
			isPastDay === undefined ? todayDay > i : isPastDay,
			isActive === undefined ? activeDayDay === i : isActive);
	}

	for (let i = 10; i <= days; i++) {
		push(i, i.toString(), (startWeekDay + i - 1) % 7, isToday === undefined ? todayDay === i : isToday,
			isPastDay === undefined ? todayDay > i : isPastDay,
			isActive === undefined ? activeDayDay === i : isActive);
	}

	return list;
};


const component = {
	Day,
};

export const theme = {
	CLASSES: 'rmd-cal--month',
	CLASSES_PAD_DAY: 'rmd-cal--pad-day',
};

const Month = ({ _theme = theme, _component = component, month, today, activeDay, onDayClick }) => {

	const { Day } = _component;

	const list = listMontDates(month, today, activeDay);

	const monthStrStart = format(month, 'YYYY-MM-');


	const padDays = Array.apply(null, { length: list[0].weekday }).map((row, i) => {
		return (
			<div
				key={ 'pad-' + monthStrStart + i }
				className={ _theme.CLASSES_PAD_DAY }
			></div>
		)
	});

	const monthDays = list.map(row => {
		return (
			<Day
				key={ row.longName }
				{ ...row }
				onClick={ onDayClick }
			/>
		)
	});

	return (
		<div className={ _theme.CLASSES }>
			{ padDays }
			{ monthDays }
		</div>
	);
};

Month.propTypes = {
	month: PropTypes.instanceOf(Date).isRequired,
	today: PropTypes.instanceOf(Date).isRequired,
	activeDay: PropTypes.instanceOf(Date).isRequired,
	onDayClick: PropTypes.func.isRequired,
};

export default Month;



