
export const handleWaveMouseDown = (refs, theme, e) => {
	if (refs.$button) {
		const $button = refs.$button;

		$button.classList.remove(theme.CLASS_ACTIVE);
		$button.classList.remove(theme.CLASS_ACTIVE_FINISH);
		$button.classList.add(theme.CLASS_ACTIVE_RESET);

		$button.dataset.expectMouseUp = '1';
		$button.dataset.timestamp = e.timeStamp;

		requestAnimationFrame(() => {
			requestAnimationFrame(() => {
				$button.classList.remove(theme.CLASS_ACTIVE_RESET);
				$button.classList.add(theme.CLASS_ACTIVE);
			});
		});
	}
};

export const handleWaveMouseUp = (refs, theme, e) => {
	if (refs.$button) {
		const $button = refs.$button;

		const downTimestamp = $button.dataset.timestamp;

		if ($button.dataset.expectMouseUp !== undefined) {
			delete $button.dataset.expectMouseUp;
		}

		const onTransitionEnd = e => {
			$button.removeEventListener('transitionend', onTransitionEnd);

			if (downTimestamp !== $button.dataset.timestamp) {
				return;
			}

			if ($button.querySelector(':active')) {
				return;
			}

			$button.classList.add(theme.CLASS_ACTIVE_RESET);
			$button.classList.remove(theme.CLASS_ACTIVE);
			$button.classList.remove(theme.CLASS_ACTIVE_FINISH);

			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					$button.classList.remove(theme.CLASS_ACTIVE_RESET);
				});
			});
		};

		const tdiff = Math.round(theme.ANIMATION_TIME_MIN - (e.timeStamp - downTimestamp));

		if (tdiff > 0) {
			setTimeout(() => {
				if (downTimestamp !== $button.dataset.timestamp) {
					return;
				}

				$button.addEventListener('transitionend', onTransitionEnd);
				$button.classList.add(theme.CLASS_ACTIVE_FINISH);
			}, tdiff);
		} else {
			$button.addEventListener('transitionend', onTransitionEnd);
			$button.classList.add(theme.CLASS_ACTIVE_FINISH);
		}
	}
};


export const handleWaveMouseLeave = (refs, theme, e) => {
	if (refs.$button) {
		const $button = refs.$button;

		if ($button.dataset.expectMouseUp) {
			handleWaveMouseUp(refs, theme, e);
		}
	}
};



