import React, { Component } from 'react';
import PropTypes from 'prop-types';

import IconButton from '../Button/IconButton';
import Slide from '../Transition/Slide';

import classNames from 'classnames';

import format from 'date-fns/format';
import isSameMonth from 'date-fns/is_same_month';


class Control extends Component {
	static IconButton = IconButton;
	static Slide = Slide;

	static CLASSES = 'rmd-cal--control';
	static CLASSES_BTN = 'rmd-cal--control-btn';
	static CLASSES_BTN_PREV = 'rmd-cal--control-btn__prev';
	static CLASSES_BTN_NEXT = 'rmd-cal--control-btn__next';
	static CLASSES_BTN_ICON = 'rmd-cal--control-btn-icon';
	static CLASSES_MONTH_NAME_CONTAINER = 'rmd-cal--control-month-name-container';
	static CLASSES_MONTH_NAME = 'rmd-cal--control-month-name';

	constructor(props) {
		super(props);

		this.state = {
			prevMonth: null,
			activeMonth: this.props.month,
		}
	}

	componentWillReceiveProps(nextProps) {

		const activeMonth = this.state.activeMonth;

		if (! isSameMonth(nextProps.month, activeMonth)) {
			this.setState({
				activeMonth: nextProps.month,
				prevMonth: this.state.activeMonth,
			});
		}
	}

	render() {

		const { IconButton, Slide } = this.constructor;

		return (
			<div className={ this.constructor.CLASSES }>
				<IconButton
					onClick={ this.props.onPrevMonth }
					className={ classNames(this.constructor.CLASSES_BTN, this.constructor.CLASSES_BTN_PREV) }
				>
					<svg className={ this.constructor.CLASSES_BTN_ICON } viewBox="0 0 24 24">
						<path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path>
					</svg>
				</IconButton>
				<div
					className={ this.constructor.CLASSES_MONTH_NAME_CONTAINER }
				>
					<Slide
						activeKey={ format(this.state.activeMonth, 'YYYY-MM') }
						animation={ this.state.activeMonth > this.state.prevMonth ? 'right-to-left' : 'left-to-right' }
					>
						<div
							data-slot="active"
							className={ this.constructor.CLASSES_MONTH_NAME }
						>
							{ format(this.state.activeMonth, 'MMMM YYYY') }
						</div>

						<div
							data-slot="prev"
							className={ this.constructor.CLASSES_MONTH_NAME }
							ref={ this.handleNextMonthRef }
						>
							{ this.state.prevMonth ?
								format(this.state.prevMonth, 'MMMM YYYY')
								: null
							}
						</div>
					</Slide>
				</div>
				<IconButton
					onClick={ this.props.onNextMonth }
					className={ classNames(this.constructor.CLASSES_BTN, this.constructor.CLASSES_BTN_NEXT) }
				>
					<svg className={ this.constructor.CLASSES_BTN_ICON } viewBox="0 0 24 24">
						<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path>
					</svg>
				</IconButton>
			</div>
		)
	}
}


Control.propTypes = {
	month: PropTypes.instanceOf(Date).isRequired,
	onPrevMonth: PropTypes.func.isRequired,
	onNextMonth: PropTypes.func.isRequired,
};

export default Control;



