// eslint-disable-next-line
import React, { Component } from 'react';

import Input, { theme as inputTheme } from './Input';

import classNames from 'classnames';

import './scss/textarea.css';

export const theme = {
	...inputTheme,
	CLASSES_WRAPPER: 'rmd-input--wrapper rmd-input--wrapper__textarea',
};

class TextArea extends Input {
	constructor(props) {
		super(props);

		this.theme = theme;
	}

	renderInput({ _theme, dark, disabled, style, inputStyle, inputClass, props  }) {
		return (
			<textarea
				{ ...props }
				disabled={ disabled }
				id={ this.id }
				style={ style || inputStyle }
				className={ classNames(_theme.CLASSES, props.class, props.className, inputClass) }
				ref={ this.handleInputRef }
				onFocus={ this.handleFocus }
				onBlur={ this.handleBlur }
			/>
		)
	}
}

export default TextArea;



