import React from 'react';
import PropTypes from 'prop-types';


import classNames from 'classnames';

import browser from '../../Common/Util/browser';

import { theme as btnTheme } from './Button';

import {
	handleWaveMouseDown,
	handleWaveMouseUp,
	handleWaveMouseLeave,
} from './Wave';

import './scss/button.css';
import './scss/button-effects.css';

import './scss/button-wave.css';
import './scss/button-wave-effects.css'

export const theme = {
	...btnTheme,
	CLASSES: 'rmd-btn rmd-btn__with-wave-o',

	CLASS_ACTIVE: 'rmd-btn__with-wave-o-active',
	CLASS_ACTIVE_FINISH: 'rmd-btn__with-wave-o-active-finish',
	CLASS_ACTIVE_RESET: 'rmd-btn__with-wave-o-active-reset',

	ANIMATION_TIME_MIN: 400,
};


const handleMouseDown = (refs, props, theme, e) => {
	handleWaveMouseDown(refs, theme, e);

	if (refs.$button) {
		const $button = refs.$button;

		const top = e.offsetY || e.nativeEvent.offsetY;
		const left = e.offsetX || e.nativeEvent.offsetX;

		$button.style.setProperty('--top', `${top}px`);
		$button.style.setProperty('--left', `${left}px`);

		if (browser.is_safari) {
			setTimeout(() => {
				const widthStyle = window.getComputedStyle(
					$button, ':before'
				).getPropertyValue('width');

				$button.style.setProperty('--rmd-btn-o--wave___height', widthStyle);
			}, 0);
		}
	}

	if (props.onMouseDown) {
		return props.onMouseDown(e);
	}
};


const handleMouseUp = (refs, props, theme, e) => {
	handleWaveMouseUp(refs, theme, e);

	if (props.onMouseUp) {
		return props.onMouseUp(e);
	}
};

const handleMouseLeave = (refs, props, theme, e) => {
	handleWaveMouseLeave(refs, theme, e);

	if (props.onMouseLeave) {
		return props.onMouseLeave(e);
	}
};

const ButtonWave = ({ _theme = theme, color, type, size, dark, children, ...props }) => {

	const refs = { $button: undefined };

	return (
		<button
			{ ...props }
			className={ classNames(_theme.CLASSES, props.class, props.className, {
				[_theme.CLASSES_FLAT]: type === 'flat' || type === undefined,
				[_theme.CLASSES_RAISED]: type === 'raised',
				[_theme.CLASSES_FAB]: type === 'fab',
				[_theme.CLASSES_DEFAULT]: color === 'default' || color === undefined,
				[_theme.CLASSES_PRIMARY]: color === 'primary',
				[_theme.CLASSES_SECONDARY]: color === 'secondary',
				[_theme.CLASSES_DISABLED]: color === 'disabled',
				[_theme.CLASSES_DENSE]: size === 'dense',
				[_theme.CLASSES_COMPACT]: size === 'compact',
				[_theme.CLASSES_DARK]: dark,
				[_theme.CLASSES_ANDROID]: browser.platform.is_android,
			}) }
			ref={ el => refs.$button = el }
			onMouseDown={ handleMouseDown.bind(undefined, refs, props, _theme) }
			onMouseUp={ handleMouseUp.bind(undefined, refs, props, _theme) }
			onMouseLeave={ handleMouseLeave.bind(undefined, refs, props, _theme) }
		>
			{ children }
		</button>
	);
};

ButtonWave.propTypes = {
	type: PropTypes.oneOf(['flat', 'raised', 'fab']),
	color: PropTypes.oneOf(['default', 'primary', 'secondary', 'disabled', 'none']),
	size: PropTypes.oneOf(['dense', 'compact']),
	dark: PropTypes.bool,
};


export default ButtonWave;



