import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';

export const theme = {
	CLASS_START_DAY: 'rmd-cal--day__wd-',
	CLASSES: 'rmd-cal--day',
	CLASSES_TODAY: 'rmd-cal--day__today',
	CLASSES_PAST: 'rmd-cal--day__past',
	CLASSES_ACTIVE: 'rmd-cal--day__active',
	CLASSES_NAME: 'rmd-cal--day-name'
};

const Day = ({ _theme = theme, shortName, isPast, isToday, weekday, isActive, longName, ...props }) => {
	return (
		<div
			{ ...props }
			data-value={ longName }
			className={ classNames(_theme.CLASSES, _theme.CLASS_START_DAY + weekday, {
				[ _theme.CLASSES_TODAY ]: isToday,
				[ _theme.CLASSES_PAST ]: isPast,
				[ _theme.CLASSES_ACTIVE ]: isActive,
			}) }
		>
			<span
				className={ _theme.CLASSES_NAME }
			>
				{ shortName }
			</span>
		</div>
	)
};


Day.propTypes = {
	shortName: PropTypes.number.isRequired,
	isPast: PropTypes.bool.isRequired,
	isToday: PropTypes.bool.isRequired,
	weekday: PropTypes.number.isRequired,

	isActive: PropTypes.bool,
};

export default Day;



