import React from 'react';
import PropTypes from 'prop-types';


import Button from '../Button/ButtonWave';

import classNames from 'classnames';

const component = {
	Button,
};

const theme = {
	CLASSES: 'rmd-menu-popup--item rmd-btn__menu-item',
};

const PopUpItem = ({ _theme = theme, _component = component, selected, ...props }) => {
	const { Button } = _component;

	return (
		<Button
			{ ...props }
			color={ selected ? 'secondary' : 'default' }
			data-selected={ selected ? '1' : undefined }
			className={ classNames(props.className, props.class, _theme.CLASSES) }
		/>
	)
};

PopUpItem.propTypes = {
	selected: PropTypes.bool,
};


export default PopUpItem;



