import ButtonV1 from './ButtonWaveV1';
import ButtonO from './ButtonWaveO';

import browser from '../../Common/Util/browser';

let Button = ButtonO;

if (browser.is_explorer || browser.is_edge) {
	Button = ButtonV1;
}

export default Button;


