import PopUp from '../Menu/PopUp';

import './scss/menu.css';

class MenuList extends PopUp {

	static CLASS_OPEN = 'rmd-menu-popup__open';

	componentDidMount() {

		this.setMenuPosition(() => {
			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					if (this._refs.$popUp) {
						this._refs.$popUp.classList.add(this.constructor.CLASS_OPEN);
					}
				});
			});
		});

		if (super.componentDidMount) {
			return super.componentDidMount.apply(this, arguments);
		}
	}


	setMenuPosition(callback) {
		const $popUp = this._refs.$popUp;

		if ($popUp) {
			const $selected = $popUp.querySelector('[data-selected]');

			if (! $selected) {
				if (callback) {
					callback();
				}

				return;
			}

			const menuListHeight = $popUp.clientHeight;
			const scrollHeight = $popUp.scrollHeight;
			const selectedOffsetTop = $selected.offsetTop;
			const selectedOffsetHeight = $selected.offsetHeight;

			let popUpTop = selectedOffsetTop + selectedOffsetHeight / 2;

			if (scrollHeight > menuListHeight) {
				$popUp.scrollTop = selectedOffsetTop + (selectedOffsetHeight / 2) - (menuListHeight / 2) ;

				if (selectedOffsetTop + (selectedOffsetHeight / 2) > menuListHeight / 2 ) {
					popUpTop = menuListHeight / 2;
				}

				const distToBottom = scrollHeight - (selectedOffsetTop + (selectedOffsetHeight / 2));

				if (distToBottom < menuListHeight / 2) {
					popUpTop = menuListHeight - distToBottom;
				}
			}


			const newState = {
				top: 'calc(' + -popUpTop + 'px + 50%)',
				transformOrigin: 'left ' + popUpTop + 'px 0',
			};

			if (this.state.top !== newState.top || this.state.transformOrigin !== newState.transformOrigin) {
				this.setState(newState, () => {
					if (callback) {
						callback();
					}
				});
			}
		}

	}
}

export default MenuList;



