let is_chrome = navigator.userAgent.indexOf('Chrome') > -1;
const is_explorer = navigator.userAgent.indexOf('MSIE') > -1 || navigator.userAgent.indexOf('Trident') > -1;
const is_firefox = navigator.userAgent.indexOf('Firefox') > -1;
let is_safari = navigator.userAgent.indexOf("Safari") > -1;
const is_edge = navigator.userAgent.indexOf("Edge") > -1;

if (is_chrome) {
	is_safari = false;
}


if (is_edge) {
	is_chrome = false;
	is_safari = false;
}

const platform = {
	is_android: navigator.userAgent.indexOf('Android') > -1,
	is_ios: navigator.userAgent.indexOf('iPad') > -1 || navigator.userAgent.indexOf('iPhone') > -1,
};

export default { platform, is_chrome, is_safari, is_firefox, is_explorer, is_edge };


