import React from 'react';

import classNames from 'classnames';

import './scss/button.css';
import './scss/button-effects.css';

export const theme = {
	CLASSES: 'rmd-btn--fab-icon',
};

const Button = ({ _theme = theme, children, ...props }) => {

	return <div
		{ ...props }
		className={ classNames(_theme.CLASSES, props.class, props.className) } >
		{ children }
	</div>;
};


export default Button;



