import React from 'react';

import classNames from 'classnames';

import setDay from 'date-fns/set_day';
import format from 'date-fns/format';

export const theme = {
	CLASSES: 'rmd-cal--wds',
	CLASSES_WD_NAME: 'rmd-cal--wd-name',

	CLASS_START_WD_NAME: 'rmd-cal--wd-name__',
};

const WDs = ({ _theme = theme }) => {

	const tempDay = new Date();

	const weekNames = Array.apply(null, {length: 7}).map((row, i) => {
		return (
			<div
				key={ 'wd-name-' + i }
				className={ classNames(_theme.CLASSES_WD_NAME, _theme.CLASS_START_WD_NAME + i) }
			>
				{ format(setDay(tempDay, i), 'dd').charAt(0) }
			</div>
		)
	});

	return (
		<div className={ _theme.CLASSES } >
			{ weekNames }
		</div>
	)
};

export default WDs;



