import React from 'react';

import IconButton from '../lib/MaterialDesignTheme/Button/IconButton';
import Text from '../lib/Common/Util/Text';
import LinkTooltip from '../lib/MaterialDesignTheme/Link/LinkTooltip';


import beachBlack from '../img/ic_beach_access_black_24px.svg';
import businesshBlack from '../img/ic_business_center_black_24px.svg';
import breackfastBlack from '../img/ic_free_breakfast_black_24px.svg';
import shareBlack from '../img/ic_share_black_24px.svg';
import starBlack from '../img/ic_star_black_24px.svg';

const btnStyleVertical = { margin: '0.25rem' };

const Tooltips = () => {
	return (
		<section className="cv-card">
			<h1>Tooltips</h1>

			<section>
				<h2>Icon Tooltip</h2>

				<div className="cv-buttons">
					<IconButton
						tooltipPosition="left"
					>
						<Text data-slot="tooltip">Tooltip left</Text>
						<img src={ shareBlack } alt="Share"/>
					</IconButton>
					<IconButton
					>
						<Text data-slot="tooltip">Tooltip top</Text>
						<img src={ shareBlack } alt="Share"/>
					</IconButton>
					<IconButton
						tooltipPosition="bottom"
					>
						<Text data-slot="tooltip">Tooltip bottom</Text>
						<img src={ shareBlack } alt="Share"/>
					</IconButton>
					<IconButton
						tooltipPosition="right"
					>
						<Text data-slot="tooltip">Tooltip right</Text>
						<img src={ shareBlack } alt="Share"/>
					</IconButton>
				</div>
				<div className="cv-buttons">
					<IconButton
						tooltipPosition="left"
						tooltipWithTriangle={true}
					>
						<Text data-slot="tooltip">Tooltip left</Text>
						<img src={ starBlack } alt="Star"/>
					</IconButton>
					<IconButton
						tooltipWithTriangle={true}
					>
						<Text data-slot="tooltip">Tooltip top</Text>
						<img src={ starBlack } alt="Star"/>
					</IconButton>
					<IconButton
						tooltipPosition="bottom"
						tooltipWithTriangle={true}
					>
						<Text data-slot="tooltip">Tooltip bottom</Text>
						<img src={ starBlack } alt="Star"/>
					</IconButton>
					<IconButton
						tooltipPosition="right"
						tooltipWithTriangle={true}
					>
						<Text data-slot="tooltip">Tooltip right</Text>
						<img src={ starBlack } alt="Star"/>
					</IconButton>
				</div>
			</section>

			<section>
				<h2>Tooltip with HTML</h2>

				<div className="cv-buttons">
					<IconButton
					>
						<Text data-slot="tooltip">Lorem <b style={{ fontWeight: 700 }}>ipsum</b> dolor <s>sit</s> <i>amet</i></Text>
						<img src={ starBlack } alt="Star"/>
					</IconButton>
				</div>

			</section>

			<section>
				<h2>Limited width tooltip</h2>
				<div className="cv-buttons cv-buttons__tooltip-limited_width">
					<div className="cv-buttons--vertical">
						<IconButton
							style={ btnStyleVertical }
							tooltipPosition="right"
							tooltipWithTriangle={ true }
							tooltipClass="cv-tooltip__max-width"
						>
							<Text data-slot="tooltip">Lorem ipsum dolor</Text>
							<img src={ beachBlack } alt="Share"/>
						</IconButton>

						<IconButton
							style={ btnStyleVertical }
							tooltipPosition="right"
							tooltipWithTriangle={ true }
							tooltipClass="cv-tooltip__max-width"
						>
							<Text data-slot="tooltip">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</Text>
							<img src={ businesshBlack } alt="Share"/>
						</IconButton>

						<IconButton
							style={ btnStyleVertical }
							tooltipPosition="right"
							tooltipWithTriangle={ true }
							tooltipClass="cv-tooltip__max-width"
						>
							<Text data-slot="tooltip">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis tincidunt sapien id neque.</Text>
							<img src={ breackfastBlack } alt="Share"/>
						</IconButton>
					</div>
				</div>
			</section>

			<section>
				<h2>Text Tooltip</h2>

				<div>
					Lorem ipsum dolor sit amet, consectetur adipiscing{' '}
					<LinkTooltip href="#">
						<Text data-slot="tooltip">Text tooltip</Text>
						elit
					</LinkTooltip>
					. Duis tincidunt sapien id neque accumsan commodo. Quisque sodales facilisis eros. Etiam tempus mi et{' '}
					<LinkTooltip href="#">
						<Text data-slot="tooltip">Text tooltip</Text>
						leo posuere
					</LinkTooltip>
					{' '}sodales. Curabitur dui est, consectetur non faucibus vitae,{' '}
					<LinkTooltip>
						<Text data-slot="tooltip">Text tooltip</Text>
						ultricies nec leo
					</LinkTooltip>
					. Nunc maximus massa justo, eget lobortis mauris tempus at. Mauris bibendum turpis at risus euismod, sit amet semper felis egestas. Aliquam cursus nisi id vestibulum fringilla.
				</div>
			</section>

		</section>
	);
};

export default Tooltips;


