import React from 'react';
import PropTypes from 'prop-types';

import browser from '../../Common/Util/browser';

import { theme } from './Button';

import classNames from 'classnames';

import './scss/button.css';
import './scss/button-effects.css';

const Button = ({ _theme = theme, color, type, size, dark, children, ...props }) => {

	return <a
		{ ...props }
		className={ classNames(_theme.CLASSES, props.class, props.className, {
			[_theme.CLASSES_FLAT]: type === 'flat' || type === undefined,
			[_theme.CLASSES_RAISED]: type === 'raised',
			[_theme.CLASSES_FAB]: type === 'fab',
			[_theme.CLASSES_DEFAULT]: color === 'default' || color === undefined,
			[_theme.CLASSES_PRIMARY]: color === 'primary',
			[_theme.CLASSES_SECONDARY]: color === 'secondary',
			[_theme.CLASSES_DISABLED]: color === 'disabled',
			[_theme.CLASSES_DENSE]: size === 'dense',
			[_theme.CLASSES_COMPACT]: size === 'compact',
			[_theme.CLASSES_DARK]: dark,
			[_theme.CLASSES_ANDROID]: browser.platform.is_android,
		}) }>
		{ children }
	</a>;
};

Button.propTypes = {
	type: PropTypes.oneOf(['flat', 'raised', 'fab']),
	color: PropTypes.oneOf(['default', 'primary', 'secondary', 'disabled', 'none']),
	size: PropTypes.oneOf(['dense', 'compact']),
	dark: PropTypes.bool,
};


export default Button;



