const slot = (children, name = undefined) => {
	const ret = {};

	const parse = child => {
		if (child.props && child.props['data-slot']) {

			if (name && name !== child.props['data-slot']) {
				return;
			}

			if (! ret[child.props['data-slot']]) {
				ret[child.props['data-slot']] = [];
			}

			ret[child.props['data-slot']].push(child);
		} else {

			if (name && name !== 'main') {
				return;
			}

			if (! ret.main) {
				ret.main = [];
			}
			ret.main.push(child);
		}
	};

	if (Array.isArray(children)) {
		for (let i = 0; i < children.length; i++) {
			const child = children[i];

			if (! child) {
				continue;
			}

			parse(child);
		}
	} else if (children) {
		parse(children);
	}

	if (name) {
		return ret[name];
	}

	return ret;
};

export default slot;


