import React from 'react';
import PropTypes from 'prop-types';

import Slide from '../Transition/Slide';

import format from 'date-fns/format';

const component = {
	Slide
};

export const theme = {
	CLASSES: 'rmd-cal--sidebar',
	CLASSES_DAY: 'rmd-cal--sidebar-day',
	CLASSES_CONTAINER_DAY: 'rmd-cal--sidebar-container-day',
	CLASSES_YEAR: 'rmd-cal--sidebar-year',
	CLASSES_CONTAINER_YEAR: 'rmd-cal--sidebar-container-year',

	CLASSES_WRAPPER: 'rmd-cal--sidebar-wrapper',
	CLASSES_WRAPPER_TOP: 'rmd-cal--sidebar-wrapper__top',
	CLASSES_WRAPPER_LEFT: 'rmd-cal--sidebar-wrapper__left',
};

const Day = ({ _theme = theme, _component = component, day, prevDay, ...props }) => {

	const { Slide } = _component;

	return (
		<div
			{ ...props }
			className={ _theme.CLASSES }
		>
			<div
				className={ _theme.CLASSES_CONTAINER_YEAR }
			>
				<Slide
					activeKey={ format(day, 'YYYY') }
					animation={ day > prevDay ? 'top-to-bottom' : 'bottom-to-top' }
				>
					<div
						data-slot="active"
						className={ _theme.CLASSES_YEAR }
					>
						{ format(day, 'YYYY') }
					</div>
					{
						prevDay ?
							<div
								data-slot="prev"
								className={ _theme.CLASSES_YEAR }
							>
								{ format(prevDay, 'YYYY') }
							</div>
							: null
					}
				</Slide>
			</div>

			<div
				className={ _theme.CLASSES_CONTAINER_DAY }
			>
				<Slide
					activeKey={ format(day, 'ddd, MMM D') }
					animation={ day > prevDay ? 'top-to-bottom' : 'bottom-to-top' }
				>
					<div
						data-slot="active"
						className={ _theme.CLASSES_DAY }
					>
						<span>
							{ format(day, 'ddd, ') }
						</span>
						<span style={{ whiteSpace: 'nowrap' }}>
							{ format(day, 'MMM D') }
						</span>
					</div>
					{
						prevDay ?
							<div
								data-slot="prev"
								className={ _theme.CLASSES_DAY }
							>
								<span>
									{ format(prevDay, 'ddd, ') }
								</span>
								<span style={{ whiteSpace: 'nowrap' }}>
									{ format(prevDay, 'MMM D') }
								</span>
							</div>
							: null
					}
				</Slide>
			</div>

		</div>
	)
};


Day.propTypes = {
	day: PropTypes.instanceOf(Date).isRequired,
	prevDay: PropTypes.instanceOf(Date),
};

export default Day;



