import React from 'react';
import PropTypes from 'prop-types';

import {
	handleMouseFocusMark,
	handleMouseFocusUnMark,
} from '../../Common/Util/setMouseFocus';


import classNames from 'classnames';

import './scss/tooltip.css';

export const theme = {
	CLASSES: 'rmd-tooltip',
	CLASSES_WRAPPER: 'rmd-tooltip--wrapper',
	CLASSES_VISUAL: 'rmd-tooltip--visual',
	CLASSES_POSITION_LEFT: 'rmd-tooltip__left',
	CLASSES_POSITION_RIGHT: 'rmd-tooltip__right',
	CLASSES_POSITION_BOTTOM: 'rmd-tooltip__bottom',

	CLASSES_WITH_TRIANGLE: 'rmd-tooltip__with-triangle',

	CLASSES_TOOLTIP_CONTAINER: 'rmd-tooltip--container',
	CLASS_TOOLTIP_CONTAINER_FOCUS: 'rmd-tooltip--container__focus',
	CLASS_TOOLTIP_CONTAINER_ACTIVE: 'rmd-tooltip--container__active',
};


const setWrapperTooltipActive = (e, $wrapper, theme) => {
	if ($wrapper) {
		$wrapper.classList.add(theme.CLASS_TOOLTIP_CONTAINER_ACTIVE);
	}
};

const setWrapperTooltipInactive = (e, $wrapper, theme) => {
	setTimeout(() => {

		if ($wrapper.querySelector(':hover')) {
			return;
		}

		if ($wrapper.classList.contains(theme.CLASS_TOOLTIP_CONTAINER_FOCUS)) {
			return;
		}

		$wrapper.classList.remove(theme.CLASS_TOOLTIP_CONTAINER_ACTIVE);

	}, theme.TOOLTIP_HIDE_ANIMATION_TIME);
};

export const handleTooltipContainerMouseEnter = (e, $wrapper, theme) => {
	if ($wrapper) {

		// Small bug, when wrapper active class moves the button position
		// and button not receive focus events
		setTimeout(() =>
				setWrapperTooltipActive(e, $wrapper, theme),
			0
		);
	}
};

export const handleTooltipContainerMouseLeave = (e, $wrapper, theme) => {
	if ($wrapper) {
		setWrapperTooltipInactive(e, $wrapper, theme);
	}
};


export const handleTooltipContainerFocus = (e, $wrapper, theme) => {
	if ($wrapper) {
		$wrapper.classList.add(theme.CLASS_TOOLTIP_CONTAINER_FOCUS);
		setWrapperTooltipActive(e, $wrapper, theme);
	}
};

export const handleTooltipContainerBlur = (e, $wrapper, theme) => {
	handleMouseFocusUnMark([ $wrapper ]);


	if ($wrapper) {
		$wrapper.classList.remove(theme.CLASS_TOOLTIP_CONTAINER_FOCUS);
		setWrapperTooltipInactive(e, $wrapper, theme);
	}
};

export const handleTooltipContainerMouseDown = (e, $wrapper, theme) => {
	handleMouseFocusMark([ $wrapper ]);
};



const Tooltip = ({ _theme = theme, position, withTriangle, children, ...props }) => {

	return (
		<div className={ theme.CLASSES_WRAPPER }>
			<div
				{ ...props }
				className={ classNames(_theme.CLASSES, props.class, props.className, {
					[ _theme.CLASSES_POSITION_LEFT ]: position === 'left',
					[ _theme.CLASSES_POSITION_RIGHT ]: position === 'right',
					[ _theme.CLASSES_POSITION_BOTTOM ]: position === 'bottom',
					[ _theme.CLASSES_WITH_TRIANGLE ]: withTriangle,
				}) }
			>
				<div className={ _theme.CLASSES_VISUAL }>
					{ children }
				</div>
			</div>
		</div>
	);
};

Tooltip.propTypes = {
 	position: PropTypes.oneOf(['top', 'left', 'bottom', 'right']),
 	withTriangle: PropTypes.bool,
};


export default Tooltip;



