import React, { Component } from 'react';

import PropTypes from 'prop-types';

import classNames from 'classnames';

import './scss/popup.css';
import './scss/popup-effects.css';

class PopUp extends Component {
	static CLASSES = 'rmd-menu-popup';
	static CLASSES_DARK = 'rmd-menu-popup__dark';
	static CLASSES_ITEMS = 'rmd-menu-popup--items';

	constructor(props) {
		super(props);

		this.handlePopUpRef = this.handlePopUpRef.bind(this);

		this.state = {
			top: undefined,
			left: undefined,
			right: undefined,
			bottom: undefined,
			transformOrigin: undefined,
		};

		this._refs = {
			$popUp: undefined,
		}
	}

	shouldComponentUpdate(nextProps, nextState) {
		return nextState !== this.state;
	}

	handlePopUpRef(el) {
		this._refs.$popUp = el;
	}

	render() {

		return (
			<div
				className={ classNames( this.constructor.CLASSES, {
					[ this.constructor.CLASSES_DARK ]: this.props.dark,
				}) }
				style={{ top: this.state.top, left: this.state.left, right: this.state.right, bottom: this.state.bottom,
					transformOrigin: this.state.transformOrigin
				}}
				ref={ this.handlePopUpRef }
			>
				<div
					className={ this.constructor.CLASSES_ITEMS }
				>
					{ this.props.children }
				</div>
			</div>
		);
	}
}

PopUp.propTypes = {
	dark: PropTypes.bool,
};

export default PopUp;



