

const forceFocus = (stateStore, eventName, $el) => {
	if (! stateStore.__mouseFocus) {
		stateStore.__mouseFocus = {
			mouseDown: false,
			focus: false,
		}
	}

	if (eventName === 'mouseDown') {
		stateStore.__mouseFocus.mouseDown = true;
	} else if (eventName === 'focus') {
		stateStore.__mouseFocus.focus = true;
	}

	setTimeout(() => {
		if (stateStore.__mouseFocus.mouseDown && ! stateStore.__mouseFocus.focus) {
			if ($el && document.activeElement !== $el) {
				// FF on Mac, Safari bug
				$el.focus();
			}
		}

		stateStore.__mouseFocus.mouseDown = false;
		stateStore.__mouseFocus.focus = false;
	}, 0);
};

export const handleMouseFocusMouseDown = (e, $el, stateStore) => {
	forceFocus(stateStore, 'mouseDown', $el);
};

export const handleMouseFocusFocus = (e, $el, stateStore) => {
	forceFocus(stateStore, 'focus', $el);
};

export const handleMouseFocusBlur = (e, $el, stateStore) => {

	// FF on Mac, Safari bug
	const valid = ! stateStore.__mouseFocus.mouseDown;

	if (! valid) {
		e.preventDefault();
		e.stopPropagation();

		return valid;
	}

	return true;
};

export const handleMouseFocusMark = ($markEls = []) => { // mouseDown
	for (let i = 0; i < $markEls.length; i++) {
		const $markEl = $markEls[i];

		if ($markEl) {
			$markEl.dataset.mouseFocus = '1';
		}
	}
};

export const handleMouseFocusUnMark = ($markEls = []) => { // blur
	for (let i = 0; i < $markEls.length; i++) {
		const $markEl = $markEls[i];

		if ($markEl && $markEl.dataset.mouseFocus) {
			delete $markEl.dataset.mouseFocus;
		}
	}
};



